import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { SharedModule } from 'app/shared/shared.module';
import { OrdemservicoListComponent } from './containers/ordemservico-list/ordemservico-list.component';
import { OrdemservicoFormComponent } from './containers/ordemservico-form/ordemservico-form.component';
import { RelatorioFormComponent } from './containers/relatorio-form/relatorio-form.component';

import { MAT_DATE_FORMATS } from '@angular/material/core';

const routes: Route[] = [
  { path: '', component: OrdemservicoListComponent },
  { path: 'novo', component: OrdemservicoFormComponent },
  { path: ':id/editar', component: OrdemservicoFormComponent },
];

export const MY_DATE_FORMATS = {
  parse: {
    dateInput: 'DD/MM/YYYY',
  },
  display: {
    dateInput: 'DD/MM/YYYY',
    monthYearLabel: 'MMM YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM YYYY',
  },
};

@NgModule({
  declarations: [
    OrdemservicoListComponent,
    OrdemservicoFormComponent,
    RelatorioFormComponent,
  ],
  imports: [CommonModule, RouterModule.forChild(routes), SharedModule],
  providers: [
    { provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS },
  ]
})
export class OrdemServicoModule { }
