import { Component, OnInit } from '@angular/core';
import {
  UntypedFormBuilder,
  UntypedFormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';

import toastr from 'toastr';

@Component({
  selector: 'app-relatorio-form',
  templateUrl: './relatorio-form.component.html',
  styleUrls: ['./relatorio-form.component.scss'],
})
export class RelatorioFormComponent implements OnInit {
  form: UntypedFormGroup;
  pageTitle: string;

  constructor(
    public dialogRef: MatDialogRef<RelatorioFormComponent>,

    private _formBuilder: UntypedFormBuilder
  ) {}

  ngOnInit(): void {
    this.criarFormularioRelatorio();
  }

  fecharDialog(filtrar: boolean): void {
    if (!filtrar) {
      this.dialogRef.close();
      return;
    }

    this.submitForm();
  }

  submitForm() {
    if (this.form.invalid) {
      return;
    }
    console.log(this.form.value)

    alert(`Ainda não gerando \n\n ${JSON.stringify(this.form.value)}`)
  }

  // PRIVATE METHODS
  private criarFormularioRelatorio() {
    this.form = this._formBuilder.group({
      nome: [null, [Validators.required, Validators.minLength(2)]],
      inicio: [new Date(), [Validators.required]],
      fim: [new Date(), [Validators.required]],
      tipo: ['TODOS', [Validators.required]],
      tipoRelatorio: ['DETALHADO', [Validators.required]],
    });
  }
}
