import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment as env } from 'environments/environment';
import { IOrdemServicoGrupoServico, IRequestOrdemServicoGrupoServico } from '../models/ordem-servico-grupo-servico.model';

@Injectable({
  providedIn: 'root',
})
export class OrdemServicoGrupoServicoService {
  apiPath: string = '/ordemServicogrupoServico';

  constructor(private _http: HttpClient) {}

  getAll(): Observable<IOrdemServicoGrupoServico[]> {
    return this._http.get<IOrdemServicoGrupoServico[]>(
      `${env.api}${this.apiPath}`
    );
  }

  getById(ordemServiceId: number, grupoServiceId: number): Observable<IOrdemServicoGrupoServico> {
    return this._http.get<IOrdemServicoGrupoServico>(`${env.api}${this.apiPath}/${ordemServiceId}/${grupoServiceId}`);
  }

  insert(resource: IRequestOrdemServicoGrupoServico): Observable<null> {
    return this._http.post<null>(`${env.api}${this.apiPath}`, resource);
  }

  update(resource: IOrdemServicoGrupoServico): Observable<null> {
    return this._http.put<null>(
      `${env.api}${this.apiPath}/${resource.id.ordemServico.id}/${resource.id.grupoServico.id}`,
      resource
    );
  }

  delete(IdOrdemServico: number, idGrupo: number): Observable<null> {
    return this._http.delete<null>(`${env.api}${this.apiPath}/${IdOrdemServico}/${idGrupo}`);
  }
}
