import { Component, Injector, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { ClienteService } from 'app/modules/ccos/cliente/api/cliente.service';
import { Cliente } from 'app/modules/ccos/cliente/models/cliente.model';
import { BaseResourceFormComponent } from 'app/shared/containers/base-resource-form';
import { IFilter } from 'app/shared/models/interface.model';
import { interval, map, Observable, startWith } from 'rxjs';
import { OrdemServicoGrupoServicoService } from '../../api/ordem-servico-grupo-servico.service';
import { OrdemServicoService } from '../../api/ordem-servico.service';
import {
  OrdemServico,
  OrdemServicoResponse,
} from '../../models/ordem-servico.model';

@Component({
  selector: 'app-ordemservico-form',
  templateUrl: './ordemservico-form.component.html',
  styleUrls: ['./ordemservico-form.component.scss'],
})
export class OrdemservicoFormComponent extends BaseResourceFormComponent<
  OrdemServico,
  OrdemServicoResponse
> {
  clientes: Cliente[] = [];

  filtro: IFilter = {
    filtro: '',
    pagina: 0,
    direcaoOrdenacao: 'DESC',
    tamanho: 10,
    ordenarPor: 'id',
  };

  datadosCarregados: boolean = false;

  constructor(
    protected orderServicoService: OrdemServicoService,
    protected injector: Injector,
    private _clienteService: ClienteService,
    private _ordemServicoGrupoServicoService: OrdemServicoGrupoServicoService
  ) {
    super(injector, new OrdemServico(), orderServicoService);
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.carregaClientes();
    this._carregaOrdemServicoGrupoServico();
  }

  submitForm(): void {
    alert(`Não implementado \n\n ${JSON.stringify(this.resourceForm.value)}`)

    console.log(this.resourceForm.value);
  }

  adicionarServico(): void {
    alert(`Não implementado!`)
  }

  carregaClientes(nome: string = ''): void {
    this.filtro.filtro = nome;
    console.log(this.filtro);
    this._clienteService.getAll(this.filtro).subscribe(
      (clientes) => {
        console.log(clientes);
        this.clientes = clientes.content;
      },
      (err) => console.log(err)
    );
  }

  displayFn(cliente: Cliente): string {
    return cliente && cliente.nome ? cliente.nome : '';
  }

  get total(): number {
    if (!this.resource.id) return 0;
    let total: number = 0;
    this.resource.grupoServico.forEach((gs) => (total = total + gs.subTotal));

    return total;
  }

  // METHODS PROTECTED

  protected buildResourceForm() {
    this.resourceForm = this.formBuilder.group({
      cliente: [null, [Validators.required]],
      data: [null, [Validators.required]],
    });

    if (this.currentAction === 'novo') {
      this.resourceForm.get('data').setValue(new Date());
    }
  }

  protected createPageTitle(): string {
    return 'Cadastro de Nova Ordem de Servico';
  }

  protected editionPageTitle(): string {
    const orderServicoCodigo = this.resource.codigoSupergas || '';
    return 'Editando Order de Servico: ' + orderServicoCodigo;
  }

  // METHODS PRIVATES

  private _carregaOrdemServicoGrupoServico(): void {
    const source = interval(500);

    source.subscribe((val) => {
      if (this.datadosCarregados) return;

      if (!this.datadosCarregados && this.resource.id) {
        this.datadosCarregados = true;
        console.log(this.resource);
        this.carregaQuantidadeESubTotal();
      }
    });
  }

  private carregaQuantidadeESubTotal(): void {
    this.resource.grupoServico.map((x) => {
      this._ordemServicoGrupoServicoService
        .getById(this.resource.id, x.id)
        .subscribe((res) => {
          x.quantidade = res.quantidade;
          x.subTotal = res.quantidade * x.valorUnitario;
        });
    });
  }
}
