import { BaseResourceModel } from 'app/shared/models/base-resource.model';

export class OrdemServico extends BaseResourceModel {
  constructor(
    public id?: number,
    public codigoSupergas?: string,
    public data?: string,
    public status?: number,
    public tipoServico?: string,
    public cliente?: ICliente,
    public grupoServico?: IGrupoServico[]
  ) {
    super();
  }
}

export class OrdemServicoResponse {
  constructor(
    public content?: OrdemServico[],
    public totalElements?: number,
    public size?: number,
    public numberOfElements?: number
  ) {}
}

export interface IGrupoServico {
  id: number;
  codigoSapItem: number;
  descSapItem: string;
  valorUnitario: number;
  descServico: string;
  tipoServico: string;
  unidadeGrupoServico: IUnidadeGrupoServico;
  tipoGrupoServico: ITipoGrupoServico;
  quantidade?: number;
  subTotal?: number;
}

export interface IUnidadeGrupoServico {
  id: number;
  nome: string;
}

export interface ITipoGrupoServico {
  id: number;
  descricao: string;
}

export interface ICliente {
  id: number;
  codigoSupergas: string;
  nome: string;
  enderecos: IEndereco[];
  cnpj: string;
  email: string;
  telefone: string;
  observacoes: string;
}

export class IEndereco {
  id: number;
  logradouro: string;
  numero: number;
  complemento: string;
  bairro: string;
  cep: string;
  cidade: Cidade;
  cidadeId: number;
  clienteId: number;
}

export class Cidade {
  constructor(
    public id?: number,
    public nome?: string,
    public estado?: Estado
  ) {}
}

export class Estado {
  constructor(
    public id?: number,
    public nome?: string,
    public codigoUf?: number,
    public uf?: string,
    public regiao?: number
  ) {}
}
