import {
  AfterContentChecked,
  Component,
  EventEmitter,
  Inject,
  Injector,
  OnInit,
} from '@angular/core';
import { FormBuilder, UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import toastr from 'toastr';

import { BaseResourceModel } from 'app/shared/models/base-resource.model';
import { BaseResourceService } from 'app/shared/services/base-resource.service';
import { EEventosLocais } from 'app/shared/models/eventos.enum';

@Component({
  template: '',
})
export abstract class BaseResourceFormComponent<T extends BaseResourceModel, R>
  implements OnInit, AfterContentChecked
{
  currentAction: string;
  resourceForm: UntypedFormGroup;
  pageTitle: string;

  protected route: ActivatedRoute;
  protected router: Router;
  protected formBuilder: FormBuilder;

  carregamentoDeDados = new EventEmitter()

  constructor(
    protected injecto: Injector,
    @Inject('resource') protected resource: T,
    protected resourceService: BaseResourceService<T, R>
  ) {
    this.route = this.injecto.get(ActivatedRoute);
    this.router = this.injecto.get(Router);
    this.formBuilder = this.injecto.get(FormBuilder);
  }

  ngOnInit(): void {
    this.setCurrentAction();
    this.buildResourceForm();
    this.loadResource();
  }

  ngAfterContentChecked(): void {
    this.setPageTitle();
  }

  submitForm() {
    if (this.resourceForm.invalid) {
      return;
    }

    const resource: T = this.resourceForm.value;
    if (this.currentAction === 'novo') {
      this.resourceService.insert(resource).subscribe(
        () => {
          toastr.success('Dado cadastrado com sucesso!', 'Sucesso', {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          });
          const baseComponentPath: string =
            this.route.snapshot.parent.url[0].path;

          this.router.navigate([baseComponentPath]);
        },
        (error) => {
          toastr.error(
            'Não foi possível carregar os dados',
            error.error.error,
            {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            }
          );
        }
      );
      return;
    }

    resource.id = this.resource.id;
    this.resourceService.update(resource).subscribe(
      () => {
        toastr.success('Dado atualizado com sucesso!', 'Sucesso', {
          timeOut: 10000,
          progressBar: true,
          closeButton: true,
        });
        const baseComponentPath: string =
          this.route.snapshot.parent.url[0].path;

        this.router.navigate([baseComponentPath]);
      },
      (error) => {
        toastr.error('Não foi possível atualizar o dado', error.error.error, {
          timeOut: 10000,
          progressBar: true,
          closeButton: true,
        });
      }
    );
  }

  // PRIVATE METHODS
  protected setCurrentAction() {
    if (this.route.snapshot.url[this.route.snapshot.url.length - 1]?.path === 'novo') {
      this.currentAction = 'novo';
    } else {
      this.currentAction = 'editar';
    }
  }

  protected loadResource() {
    if (this.currentAction === 'editar') {
      const idResource = Number(
        this.route.snapshot.url[this.route.snapshot.url.length - 2]?.path
      );
      this.resourceService.getById(idResource).subscribe(
        (resource) => {
          this.resource = resource;
          this.resourceForm.patchValue(this.resource);
          this.carregamentoDeDados.emit(EEventosLocais.CARREGAMENTO_DADOS_API)
        },
        (error) => {
          toastr.error(
            'Não foi possível carregar os dados',
            error.error.error,
            {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            }
          );
        }
      );
    }
  }

  protected setPageTitle() {
    if (this.currentAction === 'novo') {
      this.pageTitle = this.createPageTitle();
    } else {
      this.pageTitle = this.editionPageTitle();
    }
  }

  protected createPageTitle(): string {
    return 'Novo';
  }

  protected editionPageTitle(): string {
    return 'Edição';
  }

  protected abstract buildResourceForm(): void;
}
